import React, { useEffect, useState } from 'react';
import { db } from '../Firestore/Firestore';
import { collection, getDocs } from 'firebase/firestore';
import { Project, NavigationDirection, WorkProps } from '../../../types';
import PageNavigation from '../PageNavigation/PageNavigation';

import './Portfolio.css';
import '../shared-styles.css';
import border_blue from '../../../img/border_blue.svg';

const Portfolio: React.FC<WorkProps> = ({ setDialogText, setCurrentAction }) => {
  const [projects, setProjects] = useState<Project[]>([]);
  const [index, setIndex] = useState<number>(0);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchProjects = async () => {
      try {
        setIsLoading(true);
        const querySnapshot = await getDocs(collection(db, 'projects'));
        const allProjects: Project[] = [];
        querySnapshot.forEach((doc) => {
          const data = doc.data() as Project;
          allProjects.push(data);
        });

        // Preload all project images
        const imagePromises = allProjects.map((project) => {
          return new Promise((resolve) => {
            const img = new Image();
            img.onload = resolve;
            img.onerror = resolve; // Continue even if an image fails
            img.src = project.image;
          });
        });

        // Wait for all images to load
        await Promise.all(imagePromises);

        setProjects(allProjects.sort((a, b) => a.order - b.order));
      } catch (error) {
        console.error('Error fetching projects:', error);
      } finally {
        setIsLoading(false);
      }
    };
    fetchProjects();
  }, []);

  const navigatePortfolio = (direction: NavigationDirection) => {
    if (direction === 'left') {
      setIndex(index <= 0 ? projects.length - 1 : index - 1);
    }
    if (direction === 'right') {
      setIndex(index >= projects.length - 1 ? 0 : index + 1);
    }
  };

  useEffect(() => {
    const handleKeyboard = (e) => {
      if (e.keyCode === 37) navigatePortfolio('left');
      if (e.keyCode === 39) navigatePortfolio('right');
    };
    document.addEventListener('keydown', handleKeyboard);

    return () => {
      document.removeEventListener('keydown', handleKeyboard);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [index, projects.length]);

  const renderProjects = () => {
    if (isLoading) {
      return (
        <div className="project">
          <div className="project-info">
            <i className="spinner icon"></i>
            <p>Loading projects...</p>
          </div>
        </div>
      );
    }

    if (projects.length === 0) {
      return (
        <div className="project">
          <div className="project-info">
            <p>No projects available at the moment.</p>
          </div>
        </div>
      );
    }

    const project = projects[index];
    return (
      <div key={project.name} className="project">
        <div className="project-image">
          <a href={project.url} target="_blank" rel="noreferrer" title={project.name}>
            <img
              src={project.image}
              alt={project.name}
              loading="eager"
              style={{ transition: 'opacity 0.3s ease' }}
            />
          </a>
        </div>
        <div className="project-info">
          <a href={project.url} target="_blank" rel="noreferrer" title={project.name}>
            <h2>Featured Project: {project.name}</h2>
          </a>
          <p>{project.description}</p>
          {project.url && (
            <a
              href={project.url}
              target="_blank"
              className="ui blue button"
              rel="noreferrer"
              title={project.name}
              style={{ borderImageSource: `url(${border_blue})` }}
            >
              Visit Project
            </a>
          )}
          <div className="project-counter">
            <small>
              üíº PROJECT {index + 1} OF {projects.length} üíº
            </small>
          </div>
        </div>
      </div>
    );
  };

  return (
    <div id="portfolio" className="retro-page">
      <PageNavigation currentSection="goTo-portfolio" onNavigate={setCurrentAction} />

      <div className="page-content">
        <h1>Portfolio</h1>

        <div className="retro-box" style={{ textAlign: 'center', marginBottom: '20px' }}>
          Click images to view live demos!
        </div>

        <div className="projects">
          <div
            className="portfolio-nav left"
            onClick={() => navigatePortfolio('left')}
            role="button"
            tabIndex={0}
            aria-label="Previous project"
            onKeyPress={(e) => e.key === 'Enter' && navigatePortfolio('left')}
          >
            <i className="arrow alternate circle left icon"></i>
          </div>
          {renderProjects()}
          <div
            className="portfolio-nav right"
            onClick={() => navigatePortfolio('right')}
            role="button"
            tabIndex={0}
            aria-label="Next project"
            onKeyPress={(e) => e.key === 'Enter' && navigatePortfolio('right')}
          >
            <i className="arrow alternate circle right icon"></i>
          </div>
        </div>

        <div style={{ textAlign: 'center', marginTop: '30px' }}>
          <div className="visitor-counter" style={{ margin: '0 auto 20px', display: 'table' }}>
            {projects.length} Projects Available
          </div>
          <button
            className="retro-button primary"
            onClick={() => {
              if (setCurrentAction) {
                setCurrentAction('goTo-contact');
                if (setDialogText) {
                  setDialogText("I'd love to hear from you!");
                }
              }
            }}
          >
            ‚úçÔ∏è Get In Touch
          </button>
        </div>
      </div>
    </div>
  );
};

export default Portfolio;
